<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      <van-row class="title">
        <van-col span="8" offset="8">买价(bp)</van-col>
        <van-col span="8">卖价(bp)</van-col>
      </van-row>

      <van-collapse class="list" v-model="activeNames">
        <van-collapse-item :title="item.dictValue" :name="index" v-for="(item,index) in listTitle">
          <van-row v-for="item2 in listSubTitle" v-if="listData[item2.dictCode][item.dictCode]">
            <van-col span="8">{{item2.dictValue}}</van-col>
            <van-col span="8">{{listData[item2.dictCode][item.dictCode].bpBuy?listData[item2.dictCode][item.dictCode].bpBuy:'--'}}</van-col>
            <van-col span="8">{{listData[item2.dictCode][item.dictCode].bpSell?listData[item2.dictCode][item.dictCode].bpSell:'--'}}</van-col>
          </van-row>
        </van-collapse-item>
      </van-collapse>

    </van-pull-refresh>

  </div>
</template>

<script>
  export default {
    name: "creditDefaultSwap",
    data() {
      return {
        title: '信用违约互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        listTitle: [],
        listSubTitle: [],
        listData: [],
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        this.getData(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
        });
      },
      getData(fn){
        this.$http.all([
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/dict/getDictByParentCode.json?parentDictCode=grade&authorization=',
              type: 'get'
            }),
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/dict/getDictByParentCode.json?parentDictCode=term&authorization=',
              type: 'get'
            }),
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/xywy/getXywyList.json?authorization=',
              type: 'get'
            }),
        ]).then(res => {
          let listTitle = res[0];
          let listSubTitle = res[1];
          let listData = res[2];
          if (listTitle.data.success && listSubTitle.data.success && listData.data.success) {
            this.listTitle = JSON.parse(listTitle.data.data).data;
            this.listSubTitle = JSON.parse(listSubTitle.data.data).data;
            this.listData = JSON.parse(listData.data.data).data;
            fn && fn();
          } else {
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          }
        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        })
      }
    },
    created() {
      this.getData();
    }
  }
</script>

<style scoped>

  .van-pull-refresh {
    margin-top: 46px;
    background: rgba(247, 248, 250, 1);
  }
  >>>.van-pull-refresh__track {
    min-height: calc(100vh - 46px);
  }

  .title {
    background: rgba(255, 255, 255, 1);
  }
  .title .van-col {
    line-height: 40px;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(117, 117, 117, 1);
  }

  .list .van-collapse-item {
    padding-top: 10px;
  }
  >>>.list .van-cell {
    font-size: 15px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 21px;
  }
  >>>.list .van-cell__left-icon,
  >>>.list .van-cell__right-icon {
    height: 21px;
    line-height: 21px;
  }
  >>>.list .van-cell:not(:last-child)::after {
    display: none;
  }
  >>>.list .van-collapse-item__content {
    padding: 0 15px 10px;
  }
  .list [class*=van-hairline]::after {
    display: none;
  }
  .list .van-col {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(221, 169, 68, 1);
    line-height: 20px;
  }
  .list .van-col:first-child {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(124, 124, 125, 1);
    line-height: 20px;
  }

</style>



